import {
  it,
  describe,
  expect,
  inject,
  beforeEachProviders
} from '@angular/core/testing';
import { PrettyHousekeepingAssignments } from './pretty-housekeeping-assignments.pipe';

describe('PrettyHousekeepingAssignments Pipe', () => {
  beforeEachProviders(() => [PrettyHousekeepingAssignments]);

  it('return false if not passed a string', inject([PrettyHousekeepingAssignments], (pipe: PrettyHousekeepingAssignments) => {
      expect(pipe.transform(true)).toBe(false);
  }));
  it('remove underscores', inject([PrettyHousekeepingAssignments], (pipe: PrettyHousekeepingAssignments) => {
      let x = ['12_T.O._1','0_Arr_1','10_Dep_-']
      
      expect(pipe.transform(x[0])).toBe('12 T.O. 1');
      expect(pipe.transform(x[1])).toBe('0 Arr 1');
      expect(pipe.transform(x[2])).toBe('10 Dep -');
  }));
});
